import React from 'react'
import PropTypes from 'prop-types'
import DynamicComponent from '../../DynamicComponent'

const Text = ({ children, ...props }) => (
  <DynamicComponent
    color="textBody"
    fontWeight="normal"
    fontSize="medium"
    lineHeight="medium"
    {...props}
  >
    {children}
  </DynamicComponent>
)

Text.defaultProps = {
  tag: 'p',
}

Text.propTypes = {
  tag: PropTypes.string,
  children: PropTypes.node.isRequired,
}

export default Text
